
import {css, html} from 'lit';

import '@decidables/decidables-elements/slider';
import DDMMath from '@decidables/accumulable-math';

import AccumulableElement from '../accumulable-element';

/*
  DDMParameters element
  <ddm-paramters>

  Attributes:

*/
export default class DDMParameters extends AccumulableElement {
  static get properties() {
    return {
      a: {
        attribute: 'boundary-separation',
        type: Number,
        reflect: true,
      },
      z: {
        attribute: 'starting-point',
        type: Number,
        reflect: true,
      },
      v: {
        attribute: 'drift-rate',
        type: Number,
        reflect: true,
      },
      t0: {
        attribute: 'nondecision-time',
        type: Number,
        reflect: true,
      },
    };
  }

  constructor() {
    super();

    // Attributes
    this.a = undefined;
    this.z = undefined;
    this.v = undefined;
    this.t0 = undefined;
  }

  setBoundarySeparation(e) {
    this.a = +e.target.value;
    this.dispatchEvent(new CustomEvent('ddm-parameters-a', {
      detail: {
        a: this.a,
      },
      bubbles: true,
    }));
  }

  setStartingPoint(e) {
    this.z = +e.target.value;
    this.dispatchEvent(new CustomEvent('ddm-parameters-z', {
      detail: {
        z: this.z,
      },
      bubbles: true,
    }));
  }

  setDriftRate(e) {
    this.v = +e.target.value;
    this.dispatchEvent(new CustomEvent('ddm-parameters-v', {
      detail: {
        v: this.v,
      },
      bubbles: true,
    }));
  }

  setNondecisionTime(e) {
    this.t0 = +e.target.value;
    this.dispatchEvent(new CustomEvent('ddm-parameters-t0', {
      detail: {
        t0: this.t0,
      },
      bubbles: true,
    }));
  }

  static get styles() {
    return [
      super.styles,
      css`
        :host {
          display: inline-block;
        }

        .holder {
          display: flex;

          flex-direction: row;

          align-items: stretch;
          justify-content: center;
        }

        decidables-slider {
          line-height: 1;
          text-align: center;
        }

        decidables-slider  div {
          margin-bottom: 0.25rem;
        }

        .a {
          --decidables-slider-background-color: var(---color-a-light);
          --decidables-slider-color: var(---color-a);
        }

        .z {
          --decidables-slider-background-color: var(---color-z-light);
          --decidables-slider-color: var(---color-z);
        }

        .v {
          --decidables-slider-background-color: var(---color-v-light);
          --decidables-slider-color: var(---color-v);
        }

        .t0 {
          --decidables-slider-background-color: var(---color-t0-light);
          --decidables-slider-color: var(---color-t0);
        }
      `,
    ];
  }

  render() {
    return html`
      <div class="holder">
        ${this.a != null
          ? html`<decidables-slider class="a"
            ?disabled=${!this.interactive}
            scale
            min=${DDMMath.a.MIN}
            max=${DDMMath.a.MAX}
            step=${DDMMath.a.STEP}
            .value=${+this.a.toFixed(2)}
            @change=${this.setBoundarySeparation.bind(this)}
            @input=${this.setBoundarySeparation.bind(this)}
          ><div>Boundary Separation<br><span class="math-var">a</span></div></decidables-slider>`
          : html``}
        ${this.z != null
          ? html`<decidables-slider class="z"
            ?disabled=${!this.interactive}
            scale
            min=${DDMMath.z.MIN}
            max=${DDMMath.z.MAX}
            step=${DDMMath.z.STEP}
            .value=${+this.z.toFixed(2)}
            @change=${this.setStartingPoint.bind(this)}
            @input=${this.setStartingPoint.bind(this)}
          ><div>Starting Point<br><span class="math-var">z</span></div></decidables-slider>`
          : html``}
        ${this.v != null
          ? html`<decidables-slider class="v"
            ?disabled=${!this.interactive}
            scale
            min=${DDMMath.v.MIN}
            max=${DDMMath.v.MAX}
            step=${DDMMath.v.STEP}
            .value=${+this.v.toFixed(2)}
            @change=${this.setDriftRate.bind(this)}
            @input=${this.setDriftRate.bind(this)}
          ><div>Drift Rate<br><span class="math-var">v</span></div></decidables-slider>`
          : html``}
        ${this.t0 != null
          ? html`<decidables-slider class="t0" 
            ?disabled=${!this.interactive}
            scale
            min=${DDMMath.t0.MIN}
            max=${DDMMath.t0.MAX}
            step=${DDMMath.t0.STEP}
            .value=${+this.t0.toFixed(0)}
            @change=${this.setNondecisionTime.bind(this)}
            @input=${this.setNondecisionTime.bind(this)}
          ><div>Nondecision Time<br><span class="math-var">t</span></div></decidables-slider>`
          : html``}
      </div>`;
  }
}

customElements.define('ddm-parameters', DDMParameters);
